/* The customInputStyle */

.customInputStyle {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  margin: 0 18px 12px 0;
  font-size: 14px;
  font-weight: normal;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  min-width: 50%;
}

.customInputStyle.block {
  display: block;
}

/* Hide the browser's default radio button */

.customInputStyle input[type="radio"] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

.customInputStyle:hover input[type="radio"]~.checkmark {
  background-color: #ccc;
}

.customInputStyle input[type="radio"]:checked~.checkmark {
  background-color: #d71921;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.customInputStyle input[type="radio"]:checked~.checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */

.customInputStyle .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

