/* Smart Phone (portrait and landscape) */

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  /* header */
  .header-right {
    width: 100%;
  }
  .header-icon {
    width: 25px;
    height: 25px;
    margin-top: -5px;
  }
  .profilePic.px-2 .inset {
    width: 30px;
    height: 30px;
    margin-top: -5px;
  }
  .header-right li {
    text-align: center;
  }
  .navbar-nav.header-right {
    padding-left: 15px;
    margin-left: 0px;
  }
  .innerHeader .header-right {
    margin-bottom: 7.5px;
  }
  .loader:before,
  .loader:after {
    width: 6.285714rem;
    height: 6.285714rem;
    margin: 0em;
  }
  .loader {
    position: absolute;
    top: 40%;
    left: 35%;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
  }
  /* Home page */
  .personalized-message {
    margin-top: 60px;
  }
  .personalized-message p {
    font-size: 20px;
  }
  .own-detail p {
    font-size: 16px;
  }
  .no-wrap {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
  }
  .msg-drop-box {
    margin: 0 0 5% 0 !important;
    width: 100%;
  }
  .align-center {
    -ms-flex-pack: center !important;
        justify-content: center !important;
  }
  .circle {
    width: 40px;
    height: 40px;
  }
  .circle .fa {
    font-size: 18px !important;
  }
  /* planing */
  .travel-1 p {
    font-size: 18px;
  }
  .flight-details li {
    padding: 10px 0;
  }
  .flight-details .fromLocation,
  .flight-details .toLocation {
    display: block;
  }
  /* calender */
  .react-calendar {
    height: 480px;
  }
  #chat {
    height: auto !important;
  }
  .chat-input {
    position: relative;
  }
  .chat-input .row {
    margin-left: 0 !important;
    margin-right: 0 !important;
    width: 100%;
  }
  .chat-input .btn {
    width: 100%;
    display: block;
  }
  .payment-options .list-group-item small.price {
    margin-left: 40px !important;
    font-weight: bold;
  }
  .travelDetail ul li:first-child {
    min-width: 68px;
    display: none;
  }
  .Selectable .DayPicker-Month {
    border-spacing: 2px;
    margin-left: -5px;
  }
  /* bundelView */
  .flightDetail li {
    min-height: 50px;
  }
  .flightDetail .flightInfo {
    width: 100%;
  }
  .flightDetail .journeyFrmTo {
    width: 100%;
  }
  .flightDetail .journeyclassName {
    width: 100%;
    text-align: center;
  }
  .flightDetail .journeyAction {
    width: 100%;
  }
  .journeyAction small {
    display: inline-block;
    padding: 0px 20px;
  }
  .flightInfo .flightFrm,
  .flightInfo .flightTo {
    text-align: center;
  }
  .journeyDetail .HotelChkIn {
    width: 100%;
    text-align: center;
  }
  .journeyDetail .journeyAction {
    width: 100%;
    text-align: center;
  }
  .journeyDetail li {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 50px;
  }
  /* bundelView Header */
  .travelDetail ul li:first-child {
    display: none;
  }
  .travelDetail .fromLocation,
  .travelDetail .toLocation {
    width: unset;
  }
  .travelDetail ul li {
    border: unset;
  }
  .addJourneyPlan .addJourneyBtn {
    width: 45%;
  }
  .pickupMenu {
    margin: 7px 2px;
    overflow: hidden;
    text-transform: uppercase;
  }
  .pickupMenu li {
    list-style: none;
    padding: 3px 5px;
    float: left;
    margin: 7px 0px;
    font-size: 10px;
  }
  .align-mic-btn {
    margin-left: 35%;
  }
  .mt-25 {
    margin-top: 5vh;
  }
  .mt-15 {
    margin-top: 20px;
  }
  .swiper-slide .w-60 .px-3 {
    font-size: 13px;
    text-align: left !important;
  }
  /* bundelVeiw edit */
  .editPackageDetail {
    width: 100%;
  }
  .editActivityFooter,
  .editHotelFooter,
  .editFlightFooter {
    width: unset !important;
  }
  .editActivityFooter>div,
  .editHotelFooter>div,
  .editFlightFooter>div {
    height: unset !important;
    padding-top: unset !important;
  }
  .editActivity,
  .editHotel,
  .editFlight {
    padding-bottom: 150px;
  }
  .edit-btn-list {
    width: 55px !important;
  }
  .edit-search {
    width: 150px !important;
  }
  .edit-btn-filter {
    width: 130px !important;
  }
  .packageTitle {
    font-size: 26px!important;
  }
}

